import {
    Contract,
    utils,
    BigNumber
} from 'ethers'
import {
    _getProvider
} from './ethereum'
import WOOLF_ABI from './abi/woolf.abi'
import BARN_ABI from './abi/barn.abi'
import BARN_API_ABI from './abi/barn-api.abi'
const n18 = '1000000000000000000';

export const mint = async (stake, tokens, paid) => {
    const provider = _getProvider()
    if (!provider) throw new Error('Unable to connect to wallet')
    const signer = provider.getSigner()
    const contract = new Contract('0x79c6AD13b3deFE0A706557Df3540de15c0adD0A3', WOOLF_ABI, signer)
    const gasEstimate = await contract.estimateGas.mint(tokens, stake, {
        value: paid ? utils.parseUnits('.16', 'ether').mul(BigNumber.from(tokens)) : BigNumber.from(0)
    });
    return await contract.mint(tokens, stake, {
        gasLimit: gasEstimate.mul(BigNumber.from(12)).div(BigNumber.from(10)),
        value: paid ? utils.parseUnits('.16', 'ether').mul(BigNumber.from(tokens)) : BigNumber.from(0)
    })
}

export const parseMint = (staked, receipt) => {
    const woolf = new utils.Interface(WOOLF_ABI)
    const barn = new utils.Interface(BARN_ABI)

    const results = {}
    for (let x in receipt.logs) {
        try {
            const log = barn.parseLog(receipt.logs[x])
            results[log.args.tokenId.toString()] = {
                tokenId: log.args.tokenId,
                recipient: log.args.owner,
                stake: true
            }
            continue
        } catch (e) { }
        try {
            const log = woolf.parseLog(receipt.logs[x])
            results[log.args.tokenId.toString()] = {
                tokenId: log.args.tokenId,
                recipient: log.args.to,
                stake: false
            }
        } catch (e) { }
    }

    return results
}

export const tokenURI = async (tokenId) => {
    const provider = _getProvider()
    if (!provider) throw new Error('Unable to connect to wallet')
    const signer = provider.getSigner()
    const contract = new Contract('0x79c6AD13b3deFE0A706557Df3540de15c0adD0A3', WOOLF_ABI, signer)
    return await contract.tokenURI(tokenId)
}

export const loadWoolfList = async (address) => {
    const provider = _getProvider()
    if (!provider) throw new Error('Unable to connect to wallet')
    const signer = provider.getSigner()
    const contract = new Contract('0x79c6AD13b3deFE0A706557Df3540de15c0adD0A3', WOOLF_ABI, signer)
    const num = await contract.balanceOf(address); // 
    return Promise.all(new Array(parseInt(num._hex)).fill(',').map((v, i) => LoadWoolf(i))); // 
    async function LoadWoolf(i) {
        let index = await contract.tokenOfOwnerByIndex(address, i);
        const idx = parseInt(index);
        let [tokenData, tokenURI] = await Promise.all([contract.tokenTraits(index), contract.tokenURI(index)]);
        return {
            id: idx.toString(16),
            number: idx,
            owner: { id: address },
            isSheep: tokenData.isSheep,
            tokenURI: tokenURI 
        };
    }
}
const pageSize = 1000;
export const loadStakedWoolfList = async (address, total) => {
    if (total === 0) return [];
    const ids = await loadStakedWoolfList2(address, total);
    console.log(ids);
    const provider = _getProvider()
    if (!provider) throw new Error('Unable to connect to wallet')
    const signer = provider.getSigner()
    const contract = new Contract('0x79c6AD13b3deFE0A706557Df3540de15c0adD0A3', WOOLF_ABI, signer)
    return Promise.all(ids.map((i) => LoadWoolf(i))); // 
    async function LoadWoolf(i) {
        let [tokenData, tokenURI] = await Promise.all([contract.tokenTraits(i), contract.tokenURI(i)]);
        return {
            id: i.toString(16),
            number: i,
            owner: { id: address },
            isSheep: tokenData.isSheep,
            tokenURI: tokenURI 
        };
    }
}
export const loadStakedWoolfList2 = async (address, total, begin = 1, end = pageSize) => {
    const provider = _getProvider()
    if (!provider) throw new Error('Unable to connect to wallet')
    const signer = provider.getSigner()
    const contract = new Contract('0x1b18Efec79484bBB4A35FcfA6F435188Aa8C3477', BARN_API_ABI, signer);
    const { wollfs, i } = await contract.getUserWoolf('0x177A2486E0720438C61c9c3F32Cb58E151BE51B0', address, begin, end);
    const iv = parseInt(i._hex);
    const list = wollfs.map(i => parseInt(i._hex)).filter(i => i !== 0);
    console.log(list, iv);
    // return
    if (iv <= end) return list.concat(await loadStakedWoolfList2(address, total, iv + 1, iv + 1 + pageSize));
    if (end >= total) return list;
    return list.concat(await loadStakedWoolfList2(address, total, iv, Math.min(total, iv + pageSize)));
}

export const loadWoolMint = async (tokenIds) => {
    if (tokenIds.length === 0) return 0;
    const provider = _getProvider()
    if (!provider) throw new Error('Unable to connect to wallet')
    const signer = provider.getSigner()
    const contract = new Contract('0x1b18Efec79484bBB4A35FcfA6F435188Aa8C3477', BARN_API_ABI, signer);
    const res = await contract.getUserWool('0x177A2486E0720438C61c9c3F32Cb58E151BE51B0', tokenIds);
    return res.div(n18).toNumber();
};

export const loadTotalSupply = async () => {
    const provider = _getProvider()
    if (!provider) throw new Error('Unable to connect to wallet')
    const contract = new Contract('0x79c6AD13b3deFE0A706557Df3540de15c0adD0A3', WOOLF_ABI, provider)
    return await contract.minted.call()
}

export const isInvalidWoolf = (tokenURI) => {
    return tokenURI.image === 'data:image/svg+xml;base64,'
}