import { useEffect, useState } from 'react'
import { WalletHook } from "./utils/ethereum"
import { woolBalance } from './utils/wool'
import { loadTotalSupply, loadWoolfList, loadStakedWoolfList, loadWoolMint } from './utils/woolf'
import { useApollo } from './utils/apollo'
import Page from './Page'
import Modal from 'react-modal'
const App = () => {
  const { wallet, chain } = WalletHook()
  const [pageData, setPageData] = useState({
    woolclaim: 0,
    wool: '?',
    total: 0,
    woolf: [] as any[],
    stakedWoolf: [] as any[],
  });

  useEffect(() => {
    Modal.setAppElement('body');
    
    let woolclaim = 0;

    const timer = setInterval(() => {
      setPageData(v => {
        loadWoolMint(v.stakedWoolf.map(i => i.number)).then(res => {
          woolclaim = res;
        });
        return Object.assign({}, v, { woolclaim });
      })
    }, 5000);
    return () => {
      clearInterval(timer);
    }
  }, [])

  useEffect(() => {
    const loadWool = async () => {
      if (!wallet) return;
      const wool = await woolBalance(wallet);
      setPageData(v => Object.assign({}, v, { wool }))
      loadWoolf()
      loadStaked()
    }
    const loadTotal = async () => {
      const total = await loadTotalSupply();
      setPageData(v => Object.assign({}, v, { total }))
    }
    const loadWoolf = async () => {
      if (!wallet) return
      const woolf = await loadWoolfList(wallet)
      setPageData(v => Object.assign({}, v, { woolf }))
    }
    const loadStaked = async () => {
      if (!wallet) return
      const stakedWoolf = await loadStakedWoolfList(wallet, pageData.total);
      setPageData(v => Object.assign({}, v, { stakedWoolf }))
    }

    loadTotal()
    loadWool()
  }, [wallet, chain, pageData.total])

  return (
    <Page wallet={wallet} chain={chain} woolclaim={pageData.woolclaim} wool={pageData.wool} total={pageData.total} woolf={pageData.woolf} stakedWoolf={pageData.stakedWoolf} reload={async () => {
      if (!wallet) return
      const [wool, total] = await Promise.all([woolBalance(wallet), loadTotalSupply()]);
      setPageData(v => Object.assign({}, v, { wool, total }))
    }}/>
  )
}

export default App
